/* Reset some basic styles */
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
  }
  
  .navbar {
    display: flex;
    justify-content: center; /* Center the navbar items */
    align-items: center;
    background-color: #009b81;
    padding: 0;
    height: 60px;
  }
  
  .navbar-links {
    list-style: none;
    display: flex;
    margin: 0;
    height: 100%;
  }
  
  .navbar-links li {
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  .navbar-links a {
    color: white;
    text-decoration: none;
    font-size: 1rem;
    display: flex;
    justify-content: center;
    align-items: center;
    transition: background-color 0.3s ease;
    height: 100%;
    padding: 1rem;
  }
  
  .navbar-links a:hover,
  .navbar-links:focus-within > a {
    background-color: #9fc4e8; /* Change background color on hover */
  }
  
  .navbar-links a:active {
    background-color: #9fc4e8; /* Change background color on click */
  }

  .dropdown {
    height: 100%;
  }

  .dropdown-content {
    display: none;
    position: absolute;
    background-color: #009b81;
  }

  .dropdown:hover .dropdown-content {
    display: block;
  }
  